import React, { useState, useEffect, useContext } from 'react';
import PopupWithForm from './PopupWithForm';

// ÐÐ¾Ð½ÑÐµÐºÑÑ
import { ValidationFormContext } from '../../contexts/validationFormContext';
import { LogicsAllPopups } from '../../contexts/logicsAllPopups';

function AddPlacePopup({ onAddPlace, loading }) {
  const [placeName, setPlaceName] = useState('');
  const [placeImg, setPlaceImg] = useState('');

  const handleSubmit = e => {
    e.preventDefault();

    onAddPlace ({
      name: placeName,
      link: placeImg
    });

    setPlaceName('');
    setPlaceImg('');
  };

  const [isValidPlaceName, setValidPlaceName] = useState(false);
  const [isValidPlaceUrl, setValidPlaceUrl] = useState(false);

  // ÐÐ¾Ð½ÑÐµÐºÑÑ
  const validation = useContext(ValidationFormContext);
  // ÐÐ¾Ð½ÑÐµÐºÑÑ
  const {
    isAddPlacePopupOpen: isOpen,
    closeAllPopups: onClose
  } = useContext(LogicsAllPopups);

  useEffect(() => {
    const isValidPlaceName = validation(placeName, 2, 30);
    const isValidPlaceUrl = validation(placeImg, 1, 10000, 'url');

    setValidPlaceName(isValidPlaceName);
    setValidPlaceUrl(isValidPlaceUrl);
  }, [placeName, placeImg, validation]);

  const resetFormFieldsOnClose = () => {
    onClose();
    setPlaceName('');
    setPlaceImg('');
  }

  return (
    <PopupWithForm title={'ÐÐ¾Ð²Ð¾Ðµ Ð¼ÐµÑÑÐ¾'} name={'popup_add_card'} isOpen={isOpen} onClose={resetFormFieldsOnClose} onSubmit={handleSubmit} buttonText='Ð¡Ð¾Ð·Ð´Ð°ÑÑ' loading={loading}>
      <>
        <label className="popup__form-label">
          <input type="text" className="popup__form-input popup__form-input_value_place" id="place-name-input" placeholder="ÐÐ°Ð·Ð²Ð°Ð½Ð¸Ðµ" name="placeName" minLength="2" maxLength="30" required value={placeName} onChange={e => setPlaceName(e.target.value)} style={{borderBottom: !isValidPlaceName ? '1px solid red' : ''}} />
          <span className={!isValidPlaceName ? "popup__error-message place-name-input-error popup__error-message_active" : "popup__error-message place-name-input-error"}>ÐÑÐ¸Ð±ÐºÐ° Ð²Ð°Ð»Ð¸Ð´Ð°ÑÐ¸Ð¸</span>
        </label>
        <label className="popup__form-label">
          <input type="url" className="popup__form-input popup__form-input_value_img" placeholder="Ð¡ÑÑÐ»ÐºÐ° Ð½Ð° ÐºÐ°ÑÑÐ¸Ð½ÐºÑ" id="place-img-input" name="placeImg" required value={placeImg} onChange={e => setPlaceImg(e.target.value)} style={{borderBottom: !isValidPlaceUrl ? '1px solid red' : ''}} />
          <span className={!isValidPlaceUrl ? "popup__error-message place-img-input-error popup__error-message_active" : "popup__error-message place-img-input-error"}>ÐÑÐ¸Ð±ÐºÐ° Ð²Ð°Ð»Ð¸Ð´Ð°ÑÐ¸Ð¸</span>
        </label>
      </>
    </PopupWithForm>
  );
}

export default AddPlacePopup;
